.additional-products {
  padding: 0 20px;
}


.additional-products__title {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  padding: 25px 0 36px 0;
  margin: 0;
  border-bottom: 1px solid #cdcdcd;
}

@media (min-width: @tablet-width) {
  .additional-products {
    padding: 0;
  }
}


@media (min-width: @desktop-width) {
  .additional-products {
    width: 896px;
    margin-right: 80px;
  }
}
